/*
 * Landing Page Components
 */
.c-landing-header {
  position: relative;
  overflow: hidden;

  .c-landing-header__description {
    z-index: 2;
    position: relative; }

  .c-landing-header__container {
    display: flex;
    flex-direction: column;

    @include breakpoint(sm) {
      flex-direction: row; } }

  .c-landing-header__h1-container {
    margin: 0 5%;

    @include breakpoint(sm) {
      width: 60%; }

    @include breakpoint(lg) {
      width: 40%; } }

  .c-landing-header__h1 {
    @include breakpoint(sm) {
      padding: 0 2rem 0 0; }

    @include breakpoint(md) {
      padding: 0; } }

  .c-landing-header__rating {
    @include breakpoint(sm) {
      width: 40%; }

    @include breakpoint(lg) {
      width: 60%; } }

  .c-landing-header__signup {
    margin-top: 30px; }

  .c-landing-header__mockup {
    position: absolute;
    z-index: 0;
    box-shadow: -1px 5px 30px 0px rgba(0,0,0,0.4);

    transform: rotate(-16.8deg);

    @include breakpoint(xs) {
      display: none; }

    @include breakpoint(sm) {
      top: 0;
      right: -25%;
      width: 50%;
      border-radius: 7px; }

    @include breakpoint(md) {
      top: -50px;
      right: -270px;
      width: 560px;
      border-radius: 10px; }

    @include breakpoint(lg) {
      top: -100px;
      right: -410px;
      width: 800px;
      border-radius: 15px; }

    @media (min-width: 1700px) {
      top: -100px;
      right: -300px;
      width: 900px;
      transform: rotate(-14.8deg); } } }

.c-landing-rating {
  display: flex;
  justify-content: center;
  margin: 50px 5% 0 5%;

  @include breakpoint(sm) {
    margin: 0 5% 0 0; }

  @include breakpoint(md) {
    margin-right: 0; }

  .c-landing-rating__container {
    display: inline-block; }


  .c-landing-rating__drawn-text {
    position: relative;

    @include breakpoint(xs) {
      width: 200px;
      left: -60px; }

    @include breakpoint(xxs) {
      width: 200px;
      left: -0px; }

    @include breakpoint(sm) {
      width: 160px;
      left: -50px; }

    @include breakpoint(md) {
      width: 200px;
      left: -64px; }

    @include breakpoint(lg) {
      left: -80px;
      width: 250px; } }

  .c-landing-rating__wow {
    position: relative;

    @include breakpoint(xxs) {
      display: none; }


    @include breakpoint(xs) {
      width: 168px;
      right: -200px; }

    @include breakpoint(xxs) {
      width: 168px;
      right: -200px; }

    @include breakpoint(sm) {
      width: 134.4px;
      right: -100px; }

    @include breakpoint(md) {
      width: 168px;
      right: -190px; }

    @include breakpoint(lg) {
      width: 210px;
      right: -270px; } }


  .c-landing-rating__bar {
    background: $color-white;
    border: .5px $color-black solid;
    padding: 15px;
    max-width: 400px;
    margin: 5px; } }

.c-rating-bar {
  max-width: 350px;

  .c-rating-bar__star {
    width: 13.6%;
    margin-right: 8%;
    fill: rgb(255, 210, 63);

    &:last-of-type {
      margin: 0; } } }

.c-review {
  display: flex;
  flex-direction: column;
  background-color: $color-white3;
  padding: 20px;

  .c-review__bar {
    margin: auto; } }

.c-landing-content {

  .c-landing-content__h2 {
    margin-bottom: 0; }

  .c-landing-content__container {
    margin-bottom: 4em;

    @include breakpoint(md) {
      margin-bottom: 5%; }

    @include breakpoint(lg) {
      margin-bottom: 100px; }

    &:last-of-type {
      margin-bottom: 0; } }

  .c-landing-content__left {
    text-align: left; }

  .c-landing-content__right {
    text-align: right; } }


/*
 * App Components
 */

.c-chart-container {
  border: 1px #000 solid;
  padding: 20px;
  border-radius: 10px; }

.c-app-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;

  .c-app-wrapper__sidebar {
    //flex-grow: 1
    margin-right: 3%; }

  .c-app-wrapper__main {
    flex-grow: 3; } }

.c-sidebar-list {
  list-style: none;
  padding: 0;
  margin: ($padding-dashboard-li * 4) 0 0 0;

  .c-sidebar-list__li {
    font-size: $font-size-code;
    @include fontRoboto();
    text-decoration: none;

    margin-bottom: $padding-dashboard-li * 3;

    &:last-of-type {
      margin-bottom: 0; } } }

.c-sidebar-link {
  width: 100%;
  color: $color-black;
  padding: $padding-dashboard-li;
  box-sizing: border-box;
  text-decoration: none;
  display: inline-block;
  border: solid transparent 1px;

  &:hover {
    background-color: $color-white2; }

  &:focus, &:visited, &:focus-visible {
    color: $color-black;
    border: none;
    outline: none; } }

.c-sidebar-link.router-link-exact-active {
  border: solid $color-black;
  border-width: 0 0 1px 1px; }

.c-login-dialog {
  .c-login-dialog__input {
    @include fontRoboto();
    font-size: $font-size-text;
    border: 2px #000 solid;
    width: 100%;
    margin: 0;
    padding: 5px;
    outline: none;

    &:focus {
      box-shadow: 0 0 3px 0 #000; } }

  .c-login-dialog__agree-text {
    margin: 1em 0 2.5em 0;
    @include fontRoboto();
    color: $color-black4;
    font-size: $font-size-code; }

  .c-login-dialog__submit-button {
    border: none; // as it as a submit button
    @include fontRoboto();
    font-size: $font-size-text;
    text-decoration: none;
    padding: 10px;
    text-align: center;
    display: block;
    width: 100%;
    background-color: $color-black;
    color: $color-white;
    transition: all 0.5s ease;

    &:hover {
      transform: scale(1.05); } } }
